* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  /* Color Scheme */
  --color-navy: #0a192f;
  --color-navy-light: #112240;
  --color-navy-lighter: #233554;
  --color-teal: #64ffda;
  --color-teal-dark: #5ae2c0;
  --color-lavender: #8892b0;
  --color-lavender-light: #a8b2d1;
  --color-white: #e6f1ff;
  --color-gray: #ccd6f6;
  
  /* Typography */
  --font-sans: 'Poppins', 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-mono: 'Fira Code', monospace;
  
  /* Transitions */
  --transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  
  /* Animations */
  --animation-pulse: pulse 3s infinite ease-in-out;
  
  /* Box Shadows */
  --shadow-sm: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
  --shadow-md: 0 20px 30px -15px rgba(2, 12, 27, 0.7);
  --shadow-lg: 0 30px 60px rgba(0, 0, 0, 0.25);
  
  /* Border Radius */
  --border-radius: 4px;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: var(--font-sans);
  background-color: var(--color-navy);
  color: var(--color-gray);
  line-height: 1.6;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

/* Animations */
@keyframes pulse {
  0% {
    transform: scale(0.95);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(0.95);
    opacity: 0.5;
  }
}

/* Scene Container Styles */
.scene-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  transition: var(--transition);
  pointer-events: auto;
}

/* All minimized styles are now handled by inline styles in the Scene component */

/* Layout Styles */
.layout {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  pointer-events: auto;
}



/* Content Container Styles */
.content-container {
  width: 100%;
  min-height: 100vh;
  padding: 50px 150px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  pointer-events: auto;
  max-width: 1600px;
  margin: 0 auto;
}

/* Page Content Styles */
.page-content {
  width: 100%;
  padding: 0;
  margin-top: 0;
  overflow-y: visible;
  pointer-events: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}

.section-heading {
  display: flex;
  align-items: center;
  position: relative;
  margin: 10px 0 40px;
  width: 100%;
}

.section-heading .number {
  margin-right: 10px;
  color: var(--color-teal);
  font-family: var(--font-mono);
  font-size: 1.25rem;
  font-weight: 400;
}

.section-heading h2 {
  margin: 0;
  font-size: 2rem;
  font-weight: 600;
  color: var(--color-white);
}

.section-heading::after {
  content: '';
  display: block;
  height: 1px;
  width: 300px;
  background-color: var(--color-navy-lighter);
  position: relative;
  margin-left: 20px;
}

.page-content h1 {
  margin: 0 0 20px 0;
  color: var(--color-white);
  font-size: clamp(40px, 8vw, 80px);
  font-weight: 600;
  line-height: 1.1;
}

.page-content h2 {
  color: var(--color-white);
  font-size: 2rem;
  font-weight: 600;
  margin: 0 0 20px 0;
}

.page-content h3 {
  color: var(--color-white);
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0 0 15px 0;
}

.page-content p {
  margin: 0 0 15px 0;
  max-width: 540px;
  color: var(--color-lavender);
  font-size: 1.1rem;
  line-height: 1.7;
}

.page-content a {
  color: var(--color-teal);
  text-decoration: none;
  position: relative;
  transition: var(--transition);
}

.page-content a:hover {
  color: var(--color-teal-dark);
}

.page-content a::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: var(--color-teal);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.page-content a:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

@media (max-width: 1080px) {
  .content-container {
    padding: 100px 100px;
  }
}

@media (max-width: 768px) {
  .content-container {
    padding: 80px 50px;
  }
  
  .section-heading::after {
    width: 100px;
  }
}

@media (max-width: 480px) {
  .content-container {
    padding: 60px 25px;
  }
  
  .section-heading::after {
    width: 50px;
  }
}

/* Home Page Styles */
.home-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.hero-section {
  margin: 0 0 100px 0;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.hero-greeting {
  color: var(--color-teal);
  font-family: var(--font-mono);
  font-size: 1rem;
  margin-bottom: 20px;
}

.hero-title {
  color: var(--color-white);
  font-size: clamp(40px, 8vw, 80px);
  font-weight: 600;
  line-height: 1.1;
  margin: 0;
}

.hero-subtitle {
  color: var(--color-lavender);
  font-size: clamp(30px, 5vw, 50px);
  font-weight: 600;
  line-height: 1.1;
  margin: 10px 0 30px 0;
}

.hero-description {
  margin: 20px 0 30px 0;
  max-width: 540px;
  color: var(--color-lavender);
  font-size: 1.1rem;
  line-height: 1.7;
}

.highlight {
  color: var(--color-teal);
  font-weight: 500;
}

.cta-button {
  margin-top: 50px;
  color: var(--color-teal);
  background-color: transparent;
  border: 1px solid var(--color-teal);
  border-radius: var(--border-radius);
  padding: 1.25rem 1.75rem;
  font-size: 0.9rem;
  font-family: var(--font-mono);
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
  transition: var(--transition);
  display: inline-block;
}

.cta-button:hover {
  background-color: rgba(100, 255, 218, 0.1);
  transform: translateY(-3px);
  box-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
}

.featured-section {
  margin: 0 0 100px 0;
  width: 100%;
}

.featured-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  position: relative;
  margin-top: 50px;
}

.featured-card {
  position: relative;
  background-color: var(--color-navy-light);
  border-radius: var(--border-radius);
  padding: 2rem 1.75rem;
  transition: var(--transition);
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.featured-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-md);
}

.featured-card-icon {
  color: var(--color-teal);
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.featured-card h3 {
  color: var(--color-white);
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.featured-card p {
  color: var(--color-lavender);
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 20px;
  flex-grow: 1;
}

.featured-card-link {
  color: var(--color-teal);
  font-family: var(--font-mono);
  font-size: 0.9rem;
  text-decoration: none;
  position: relative;
  display: inline-flex;
  align-items: center;
}

.featured-card-link i {
  margin-left: 5px;
  transition: var(--transition);
}

.featured-card-link:hover i {
  transform: translateX(5px);
}

@media (max-width: 768px) {
  .hero-section {
    margin-bottom: 70px;
  }
  
  .featured-section {
    margin-bottom: 70px;
  }
  
  .featured-grid {
    grid-template-columns: 1fr;
  }
}

/* About Page Styles */
.about-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.about-grid {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 50px;
  margin-top: 50px;
}

.about-text {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.about-text p {
  margin: 0;
  color: var(--color-lavender);
  font-size: 1.1rem;
  line-height: 1.7;
}

.about-skills {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.about-skills h3 {
  color: var(--color-white);
  font-size: 1.3rem;
  margin: 0 0 15px 0;
}

.skills-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(140px, 200px));
  gap: 0 10px;
  padding: 0;
  margin: 20px 0 0 0;
  overflow: hidden;
  list-style: none;
}

.skills-list li {
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
  font-family: var(--font-mono);
  font-size: 0.9rem;
  color: var(--color-lavender);
}

.skills-list li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--color-teal);
  font-size: 0.9rem;
  line-height: 1.5;
}

.about-image-container {
  position: relative;
  max-width: 300px;
  margin: 0 auto;
}

.about-image-wrapper {
  position: relative;
  display: block;
  width: 100%;
  border-radius: var(--border-radius);
  background-color: var(--color-teal);
}

.about-image {
  position: relative;
  border-radius: var(--border-radius);
  mix-blend-mode: multiply;
  filter: grayscale(100%) contrast(1);
  transition: var(--transition);
  width: 100%;
  height: auto;
}

.about-image-wrapper:hover .about-image {
  filter: none;
  mix-blend-mode: normal;
}

.about-image-wrapper::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: var(--border-radius);
  border: 2px solid var(--color-teal);
  top: 20px;
  left: 20px;
  z-index: -1;
  transition: var(--transition);
}

.about-image-wrapper:hover::after {
  top: 15px;
  left: 15px;
}

.about-cta {
  margin-top: 50px;
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--color-white);
  display: flex;
  align-items: center;
  gap: 10px;
}

.about-cta i {
  color: var(--color-teal);
}

@media (max-width: 768px) {
  .about-grid {
    grid-template-columns: 1fr;
    gap: 50px;
  }
  
  .about-image-container {
    margin: 50px auto 0;
    width: 70%;
  }
}

@media (max-width: 480px) {
  .skills-list {
    grid-template-columns: 1fr;
  }
  
  .about-image-container {
    width: 100%;
  }
}

/* Skills Page Styles */
.skills-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.skills-intro {
  max-width: 600px;
  margin-bottom: 50px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 30px;
  width: 100%;
  margin-top: 50px;
}

.skill-category {
  background-color: var(--color-navy-light);
  border-radius: var(--border-radius);
  padding: 2rem;
  transition: var(--transition);
  box-shadow: var(--shadow-sm);
}

.skill-category:hover {
  transform: translateY(-7px);
  box-shadow: var(--shadow-md);
}

.skill-category h3 {
  color: var(--color-white);
  font-size: 1.5rem;
  margin: 0 0 20px 0;
  border-bottom: 1px solid var(--color-navy-lighter);
  padding-bottom: 10px;
}

.skill-category-icon {
  color: var(--color-teal);
  font-size: 1.5rem;
  margin-right: 10px;
}

.skill-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skill-list li {
  position: relative;
  padding: 10px 0 10px 25px;
  color: var(--color-lavender);
  font-size: 1rem;
}

.skill-list li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--color-teal);
  font-size: 1rem;
}

.skill-level {
  margin-top: 5px;
  height: 5px;
  background-color: var(--color-navy-lighter);
  border-radius: 5px;
  overflow: hidden;
}

.skill-level-fill {
  height: 100%;
  background-color: var(--color-teal);
  border-radius: 5px;
}

.skill-tag {
  display: inline-block;
  background-color: rgba(100, 255, 218, 0.1);
  color: var(--color-teal);
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 0.8rem;
  margin: 5px 5px 5px 0;
  font-family: var(--font-mono);
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

/* Projects Page Styles */
.projects-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.projects-intro {
  max-width: 600px;
  margin-bottom: 50px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  width: 100%;
  margin-top: 50px;
}

.project-card {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: var(--color-navy-light);
  border-radius: var(--border-radius);
  overflow: hidden;
  transition: var(--transition);
  height: 100%;
  box-shadow: var(--shadow-sm);
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-md);
}

.project-image {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  background-color: var(--color-navy-lighter);
  overflow: hidden;
}

.project-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.project-card:hover .project-image img {
  transform: scale(1.05);
}

.placeholder-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  font-weight: bold;
  color: var(--color-teal);
  background-color: var(--color-navy-lighter);
}

.project-content {
  padding: 25px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.project-header .folder-icon {
  color: var(--color-teal);
  font-size: 2rem;
}

.project-links {
  display: flex;
  gap: 15px;
}

.project-links a {
  color: var(--color-lavender-light);
  font-size: 1.2rem;
  transition: var(--transition);
}

.project-links a:hover {
  color: var(--color-teal);
  transform: translateY(-3px);
}

.project-title {
  color: var(--color-white);
  font-size: 1.5rem;
  margin: 0 0 10px 0;
  transition: var(--transition);
}

.project-card:hover .project-title {
  color: var(--color-teal);
}

.project-description {
  color: var(--color-lavender);
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 20px;
  flex-grow: 1;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: auto;
  font-family: var(--font-mono);
  font-size: 0.8rem;
  color: var(--color-lavender-light);
}

.featured-project {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 10px;
  align-items: center;
  margin-bottom: 100px;
}

.featured-project-content {
  grid-column: 1 / 8;
  grid-row: 1 / -1;
  position: relative;
  z-index: 2;
}

.featured-project-image {
  grid-column: 6 / -1;
  grid-row: 1 / -1;
  position: relative;
  z-index: 1;
  height: 0;
  padding-bottom: 56.25%;
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow-md);
}

.featured-project-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: grayscale(100%) contrast(1) brightness(90%);
  mix-blend-mode: multiply;
  transition: var(--transition);
}

.featured-project:hover .featured-project-image img {
  filter: none;
  mix-blend-mode: normal;
}

.featured-project.right .featured-project-content {
  grid-column: 6 / -1;
  text-align: right;
}

.featured-project.right .featured-project-image {
  grid-column: 1 / 8;
}

.featured-project-overline {
  font-family: var(--font-mono);
  font-size: 0.9rem;
  color: var(--color-teal);
  margin: 0 0 10px 0;
}

.featured-project-title {
  color: var(--color-white);
  font-size: 2rem;
  margin: 0 0 20px 0;
}

.featured-project-description {
  background-color: var(--color-navy-light);
  padding: 25px;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-sm);
  color: var(--color-lavender);
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 20px;
}

.featured-project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin: 25px 0;
  font-family: var(--font-mono);
  font-size: 0.9rem;
  color: var(--color-lavender-light);
}

.featured-project.right .featured-project-tech {
  justify-content: flex-end;
}

.featured-project-links {
  display: flex;
  gap: 15px;
}

.featured-project.right .featured-project-links {
  justify-content: flex-end;
}

.featured-project-links a {
  color: var(--color-lavender-light);
  font-size: 1.2rem;
  transition: var(--transition);
}

.featured-project-links a:hover {
  color: var(--color-teal);
  transform: translateY(-3px);
}

@media (max-width: 768px) {
  .featured-project {
    display: flex;
    flex-direction: column;
    margin-bottom: 70px;
  }
  
  .featured-project-content,
  .featured-project.right .featured-project-content {
    text-align: left;
    width: 100%;
    z-index: 2;
    padding: 0;
  }
  
  .featured-project-image,
  .featured-project.right .featured-project-image {
    width: 100%;
    margin: 40px 0 0 0;
  }
  
  .featured-project-tech,
  .featured-project.right .featured-project-tech,
  .featured-project-links,
  .featured-project.right .featured-project-links {
    justify-content: flex-start;
  }
  
  .projects-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media (max-width: 480px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
}

/* Contact Page Styles */
.contact-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contact-intro {
  max-width: 600px;
  margin-bottom: 50px;
}

.contact-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  width: 100%;
  margin-top: 50px;
}

.contact-form-container {
  width: 100%;
}

.contact-form {
  width: 100%;
  margin: 0;
}

.form-group {
  margin-bottom: 25px;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 10px;
  font-family: var(--font-mono);
  font-size: 0.9rem;
  color: var(--color-teal);
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 15px;
  border-radius: var(--border-radius);
  border: 1px solid var(--color-navy-lighter);
  background-color: var(--color-navy-light);
  color: var(--color-white);
  font-family: var(--font-sans);
  font-size: 1rem;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--color-teal);
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.1);
}

.form-group textarea {
  min-height: 150px;
  resize: vertical;
}

.submit-btn {
  background-color: transparent;
  color: var(--color-teal);
  border: 1px solid var(--color-teal);
  padding: 15px 25px;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-family: var(--font-mono);
  font-size: 0.9rem;
  transition: var(--transition);
  margin-top: 10px;
}

.submit-btn:hover {
  background-color: rgba(100, 255, 218, 0.1);
  transform: translateY(-3px);
  box-shadow: var(--shadow-sm);
}

.contact-info {
  width: 100%;
}

.contact-info h3 {
  color: var(--color-white);
  font-size: 1.5rem;
  margin: 0 0 30px 0;
}

.social-links {
  margin-top: 0;
}

.social-icons {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.social-icons a.contact-icon {
  color: var(--color-lavender);
  text-decoration: none;
  padding: 15px 20px;
  background-color: var(--color-navy-light);
  border-radius: var(--border-radius);
  transition: var(--transition);
  display: flex;
  align-items: center;
  border: 1px solid var(--color-navy-lighter);
}

.social-icons a.contact-icon:hover {
  background-color: var(--color-navy-lighter);
  transform: translateY(-5px);
  color: var(--color-teal);
  border-color: var(--color-teal);
  box-shadow: var(--shadow-sm);
}

.social-icons .icon {
  font-size: 1.5rem;
  margin-right: 15px;
  display: inline-block;
  min-width: 30px;
  text-align: center;
  color: var(--color-teal);
}

.contact-alternative {
  margin-top: 50px;
  text-align: center;
  width: 100%;
  padding: 30px;
  background-color: var(--color-navy-light);
  border-radius: var(--border-radius);
  border: 1px solid var(--color-navy-lighter);
}

.contact-alternative h3 {
  color: var(--color-white);
  font-size: 1.5rem;
  margin: 0 0 15px 0;
}

.contact-alternative p {
  margin: 0 auto 20px;
  max-width: 500px;
}

.contact-alternative .email-link {
  font-family: var(--font-mono);
  font-size: 1.2rem;
  color: var(--color-teal);
  text-decoration: none;
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  border: 1px solid var(--color-teal);
  border-radius: var(--border-radius);
  margin-top: 10px;
  transition: var(--transition);
}

.contact-alternative .email-link:hover {
  background-color: rgba(100, 255, 218, 0.1);
  transform: translateY(-3px);
  box-shadow: var(--shadow-sm);
}

@media (max-width: 768px) {
  .contact-grid {
    grid-template-columns: 1fr;
    gap: 50px;
  }
  
  .contact-info {
    order: 2;
  }
  
  .contact-form-container {
    order: 1;
  }
}

/* Responsive Styles */
@media (max-width: 768px) {
  .page-content {
    padding: 80px 20px;
  }
  
  .section-heading h2 {
    font-size: 1.5rem;
  }
  
  .section-heading h2::after {
    width: 150px;
  }
  
  .hero-greeting {
    font-size: 1rem;
  }
  
  .hero-title {
    font-size: 2.5rem;
    margin: 10px 0;
  }
  
  .hero-subtitle {
    font-size: 2rem;
  }
  
  .hero-description {
    font-size: 1rem;
    max-width: 100%;
  }
  
  .featured-grid {
    grid-template-columns: 1fr;
  }
  
  .about-grid {
    grid-template-columns: 1fr;
  }
  
  .about-image-container {
    margin-top: 50px;
    max-width: 300px;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .project-card {
    flex-direction: column;
  }
  
  .project-image {
    width: 100%;
    height: 200px;
    margin-right: 0;
    margin-bottom: 20px;
  }
  
  .featured-project {
    margin-bottom: 70px;
  }
  
  .featured-project-content {
    padding: 20px;
  }
  
  .featured-project-image {
    opacity: 0.1;
  }
  
  .featured-project-title {
    font-size: 1.5rem;
  }
  
  .featured-project-description {
    padding: 0;
    box-shadow: none;
    background-color: transparent;
  }
  
  .featured-project-tech {
    flex-wrap: wrap;
  }
  
  .header {
    padding: 15px;
  }
  
  .nav-links {
    display: none; /* We'll implement a mobile menu in a future update */
  }
  
  .scene-container.minimized {
    width: 80px;
    height: 80px;
    bottom: 20px;
    right: 20px;
  }
}
